.preview {
	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__info {
		&-title {
			font-family: PT Mono;
			font-style: normal;
			font-weight: bold;
			font-size: 44px;
			line-height: 64px;
			letter-spacing: -0.02em;
			color: #242433;
			margin-bottom: 24px;
		}
		&-text{
			color: #656475;
			margin-bottom: 60px;
			max-width: 536px;
		}
		&-link{
			display: flex;
			align-items: center;
			svg{
				margin-left: 8px;
			}
		}
	}
	&__img-wrap{
		position: relative;
		font-size: 0;
		&::before{
			content: '';
			position: absolute;
			width: 415px;
			height: 415px;
			background: #B1B0BF;
			bottom: -12px;
			right: -12px;
			z-index: -1;
		}
	}
}